/*cormorant garamond*/
@font-face {
  font-family: "Cormorant Garamond";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/cormorant-garamond-v10-latin-ext-400-italic.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-v10-latin-ext-400-italic.woff") format("woff");
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src:
    url("../fonts/cormorant-garamond-v10-latin-ext-600.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-v10-latin-ext-600.woff") format("woff");
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src:
    url("../fonts/cormorant-garamond-v10-latin-ext-700.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-v10-latin-ext-700.woff") format("woff");
}

