.page-header {
  outline: 2px solid red;

  @media (max-width: $desktop-width) {
    background-color: $layout-brown;
  }
}

.page-header__top-wrapper {
  display: flex;
  box-sizing: border-box;
  padding-left: 98px;
  padding-right: 98px;
  padding-top: 30px;
  padding-bottom: 19px;
  justify-content: space-between;

  @media (max-width: $desktop-width) {
    padding: 0;
    padding-top: 30px;
    padding-left: 45px;
    padding-right: 45px;
  }

  @media (max-width: $tablet-width) {
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.page-header__bottom-wrapper {
  display: flex;
  justify-content: center;
  border-top: 1px solid $layout-white-darker;
  box-sizing: border-box;
  padding-left: 98px;
  padding-right: 98px;

  @media (max-width: $desktop-width) {
    justify-content: stretch;
    border: none;
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 40px;
    padding-bottom: 80px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 30px;
    padding-bottom: 50px;
  }
}

.page-header__cart-wrapper {
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-start;
  flex-wrap: wrap;
  padding-top: 17px;
  margin-left: 10px;

  @media (max-width: $desktop-width) {
    width: auto;
    padding: 0;
    margin: 0;
    margin-top: 25px;
  }

  @media (max-width: $tablet-width) {
    margin-top: 20px;
  }
}

.page-header__burger-button {
  display: none;

  @media (max-width: $desktop-width) {
    display: block;
    order: -1;
    margin: 0;
    padding: 0;
    margin-top: 25px;
    width: 25px;
    height: 16px;
    background-color: transparent;
    border: none;
    cursor: pointer;

    &:focus {
      outline: none;
      opacity: 0.7;

      .page-header__svg {
        stroke: $layout-black;
      }
    }

    &:hover {
      .page-header__svg {
        stroke: $layout-black;
      }
    }
  }

  @media (max-width: $tablet-width) {
    margin-top: 20px;
  }
}

.page-header__svg {
  display: none;

  @media (max-width: $desktop-width) {
    display: block;
    stroke: $layout-white;
    fill: transparent;
  }
}
